import React, { useEffect, useState } from "react";

import { Row, Col, Input, Card, Pagination, Form } from "antd";
import { Table, Space } from "antd";
import Swal from "sweetalert2";

import { Search } from "react-iconly";
import { connect } from "react-redux";

import MasterPageTitle from "../../../../layout/components/content/master-page-title";

import {
  getMechanics,
  mechanicsEdit,
  mechanicDelete,
  isModelVisible,
} from "../../../../store/Masters/Mechanics/mechanicsSlice";

import AddMechanics from "./addMechanics";

function Mechanics(props) {
  useEffect(() => {
    props.getMechanics({ page: 1, search: "", page_size: 10 });
    // props.getMiniFirm();
  }, []);
  const columnClass =
    "hp-p1-body hp-text-color-black-50 hp-text-color-dark-0 hp-font-weight-500 ";
  const columns = [
    {
      title: "S.No",
      dataIndex: "index",
      width: "5%",
      // sorter: (a, b) => a.index - b.index,
    },
    {
      title: "Code",
      dataIndex: "code",
      // onFilter: (value, record) => record.address.startsWith(value),
      filterSearch: true,
      width: "20%",
      render: (text) => <span className={columnClass}>{text}</span>,
    },
    {
      title: "Mechanics Name",
      dataIndex: "name",

      filterMode: "tree",
      filterSearch: true,
      // onFilter: (value, record) => record.name.includes(value),
      width: "20%",
    },
    {
      title: "Mobile",
      filterMode: "tree",
      dataIndex: "mobile",
      // onFilter: (value, record) => record.name.includes(value),
      width: "20%",
      render: (text) => <span className={columnClass}>{text}</span>,
    },
    {
      title: "Aadhar Number",
      filterMode: "tree",
      dataIndex: "aadharno",
      // onFilter: (value, record) => record.name.includes(value),
      width: "20%",
      render: (text) => <span className={columnClass}>{text}</span>,
    },
    {
      title: "Account Number",
      filterMode: "tree",
      dataIndex: "accono",
      // onFilter: (value, record) => record.name.includes(value),
      width: "20%",
      render: (text) => <span className={columnClass}>{text}</span>,
    },
    {
      title: "Bank",
      filterMode: "tree",
      filterSearch: true,
      // onFilter: (value, record) => record.name.includes(value),
      width: "20%",
      render: (row) => <span className={columnClass}>{row.bank.name}</span>,
    },
    {
      title: "IFSC CODE",
      filterMode: "tree",
      dataIndex: "ifsccode",
      // onFilter: (value, record) => record.name.includes(value),
      width: "20%",
      render: (text) => <span className={columnClass}>{text}</span>,
    },
    {
      title: "Address",
      filterMode: "tree",
      dataIndex: "address",
      // onFilter: (value, record) => record.name.includes(value),
      width: "20%",
      render: (text) => <span className={columnClass}>{text}</span>,
    },
    {
      title: "Actions",
      key: "action",
      sorter: true,
      width: "10%",
      render: (row, record) => (
        <Space size="middle">
          <i
            className="ri-edit-box-line hp-mr-2 "
            style={{ cursor: "pointer", color: "#fc6f03" }}
            onClick={() => {
              props.mechanicsEdit(row);
              props.isModelVisible(true);
            }}
          ></i>
          <i
            className="ri-delete-bin-2-line "
            style={{ cursor: "pointer", color: "#ff0000" }}
            onClick={() => deleteRecord(row)}
          ></i>
        </Space>
      ),
    },
  ];

  const data = props.mechanicslist.map((row, key) => ({
    ...row,
    index: key + 1,
    // mobilename: row.mobile.name,
    // aadharnoname: row.aadharno.name,
    // ifsccodename: row.ifsccode.name,
    // accononame: row.accono.name,
    // addressname: row.address.name,
  }));

  function deleteRecord(record) {
    Swal.fire({
      title: "Are you sure?",
      text: "You won't be able to revert this!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, delete it!",
    }).then((result) => {
      if (result.isConfirmed) {
        props.mechanicDelete(record.id);
      }
    });
  }

  // function onShowSizeChange(current, pageSize) {
  //   props.getCountrys({ page_size: pageSize, page: 1 });
  // }
  function onPageChange(page, pageSize) {
    props.getMechanics({ page_size: pageSize, page: page == 0 ? 1 : page });
  }

  function onSearch(val) {
    props.getMechanics({ page: 1, search: val });
  }
  function showTotal(total) {
    return "Total " + total + " items";
  }
  return (
    <Row gutter={[32, 32]} className="hp-mt-32">
      <MasterPageTitle
        pageTitle="Mechanics Master"
        addModelTile="Add Mechanics"
        goBack={true}
        setIsModalVisible={props.isModelVisible}
        isModalVisible={props.model}
      />

      <Col span={24}>
        <Card>
          <Row className="hp-mb-12">
            <Col span={12}>
              <Input
                className="hp-mb-12"
                placeholder="Default"
                prefix={
                  <Search set="curved" className="remix-icon" size={16} />
                }
                onChange={(e) => {
                  if (e.target.value == "") {
                    props.getMechanics({ page: 1, search: "" });
                  } else {
                    onSearch(e.target.value);
                  }
                }}
              />
            </Col>

            <Col span={24}>
              <Table
                columns={columns}
                dataSource={data}
                scroll={{ x: 500 }}
                pagination={false}
                size="middle"
              />
            </Col>
            <Col span={24}>
              <Pagination
                size="small"
                // current={props.?.page}
                // pageSize={props.?.page_size}
                total={props.listCount}
                showTotal={showTotal}
                showSizeChanger
                // onShowSizeChange={onShowSizeChange}
                onChange={onPageChange}
                on
                className="hp-mb-12 hp-mt-24 hp-float-right"
              />
            </Col>
          </Row>
        </Card>
      </Col>
      <AddMechanics />
    </Row>
  );
}

function mapStateToProps({ mechanics }) {
  return {
    mechanicslist: mechanics.mechanicslist,
    noofpages: mechanics.noofpages,
    listCount: mechanics.listCount,
  };
}

export default connect(mapStateToProps, {
  getMechanics,
  mechanicsEdit,
  mechanicDelete,
  isModelVisible,
  // getMiniFirm,
})(Mechanics);
