import { Fragment, useState, useEffect } from "react";

import { connect } from "react-redux";

import "flatpickr/dist/themes/light.css";
import { RiCloseFill } from "react-icons/ri";
import { Button, Input, Form, Modal, Badge, Row, Col, Select } from "antd";
import {
  mechanicAdd,
  isModelVisible,
  InputChangeValue,
} from "../../../../store/Masters/Mechanics/mechanicsSlice";
import { getMiniBanks } from "../../../../store/mini/miniSlice";
import NumericInput from "../../../components/data-entry/inputs/numberInput";

export const isObjEmpty = (obj) => Object.keys(obj).length === 0;

const AddMechanics = (props) => {
  useEffect(() => {
    props.getMiniBanks();
  }, []);
  function ChangeHandeler(e) {
    props.InputChangeValue({
      key: e.target.name,
      value: e.target.value,
    });
  }

  console.log("props.rowdata", props.rowdata);

  // Adds New Country
  const handleAdd = () => {
    const obj = {
      id: 0,
      name: props.rowdata?.name,
      bank_id: props.rowdata.bank_id,
      mobile: props.rowdata.mobile,
      aadharno: props.rowdata.aadharno,
      accono: props.rowdata.accono,
      ifsccode: props.rowdata.ifsccode,
      address: props.rowdata.address,
    };
    if (obj.name != null || obj.name == "") {
      props.mechanicAdd(obj);
    }
  };
  // Updates Country in Store
  const handleUpdate = () => {
    const obj = {
      id: props.rowdata?.id,
      name: props.rowdata?.name,
      bank_id: props.rowdata.bank_id,
      mobile: props.rowdata.mobile,
      aadharno: props.rowdata.aadharno,
      accono: props.rowdata.accono,
      ifsccode: props.rowdata.ifsccode,
      address: props.rowdata.address,
    };
    props.mechanicAdd(obj);
  };

  // Reset Input Values on Close
  const handleResetInputValues = () => {
    props.isModelVisible(false);
  };

  const FooterAddOrUpdate = () => {
    if (props.rowdata.id == null || props.rowdata.id == 0) {
      return (
        <Fragment>
          <Button
            onClick={() => {
              handleAdd();
            }}
            type="primary"
            block
          >
            Add
          </Button>
        </Fragment>
      );
    } else {
      return (
        <Fragment>
          <Button
            type="primary"
            onClick={() => {
              handleUpdate();
            }}
            block
          >
            Update
          </Button>
        </Fragment>
      );
    }
  };

  const FooterCancel = () => {
    return (
      <Fragment>
        <Button
          onClick={() => {
            handleResetInputValues();
          }}
          type="primary"
          block
          ghost
        >
          Cancel
        </Button>
      </Fragment>
    );
  };

  const modalTitle = (
    <h5 className="modal-title">
      {props.rowdata.id == null || props.rowdata.id == 0 ? "Add" : "Update"}{" "}
      mechanics
    </h5>
  );

  return (
    <Modal
      visible={props.model}
      title={modalTitle}
      onCancel={() => {
        handleResetInputValues();
        props.isModelVisible(false);
      }}
      className="hp-modal-p-24"
      footer={
        <Row justify="center">
          <Col span={12} className="hp-pr-4">
            <FooterCancel />
          </Col>

          <Col span={12} className="hp-pl-4">
            <FooterAddOrUpdate />
          </Col>
        </Row>
      }
      centered
      closeIcon={
        <RiCloseFill className="remix-icon text-color-black-100" size={24} />
      }
      width={716}
    >
      <Form layout="vertical">
        <Row>
          <Col md={8} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item label="Bank :">
              <Select
                showSearch
                placeholder="Select a Bank"
                optionFilterProp="Firm"
                onChange={(value) => {
                  props.InputChangeValue({
                    key: "bank_id",
                    value: value,
                  });
                }}
                value={
                  props.rowdata.bank_id == null ? "" : props.rowdata?.bank_id
                }
              >
                {props.miniBanksList.map((bank, index) => {
                  return (
                    <Option key={index} value={bank.id}>
                      {bank.name}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Col>

          <Col md={8} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item label="Mobile :" getValueProps={() => {}}>
              <NumericInput
                id="Mobile"
                name="mobile"
                style={{ width: "100%" }}
                placeholder="Mobile Number"
                maxLength={10}
                value={props.rowdata?.mobile}
                onChange={props.InputChangeValue}
              />
            </Form.Item>
          </Col>
          <Col md={8} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item label="Aadhar Number :" getValueProps={() => {}}>
              <NumericInput
                id="Aadharno"
                name="aadharno"
                style={{ width: "100%" }}
                placeholder="Aadharno Number"
                maxLength={12}
                value={props.rowdata?.aadharno}
                onChange={props.InputChangeValue}
              />
            </Form.Item>
          </Col>

          <Col md={8} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item label="Account Number :" getValueProps={() => {}}>
              <NumericInput
                id="accono"
                name="accono"
                style={{ width: "100%" }}
                placeholder="Account Number"
                maxLength={12}
                value={props.rowdata?.accono}
                onChange={props.InputChangeValue}
              />
            </Form.Item>
          </Col>

          <Col md={8} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item label="IFSC Code :">
              <Input
                id="IFSC-Code"
                name="ifsccode"
                style={{ width: "100%" }}
                placeholder="IFSC Code"
                value={props.rowdata?.ifsccode}
                onChange={ChangeHandeler}
                rules={[
                  { required: true, message: "Please input your IFSC Code!" },
                ]}
              />
            </Form.Item>
          </Col>

          <Col md={8} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item label="Address :">
              <Input
                id="Address"
                name="address"
                style={{ width: "100%" }}
                placeholder="Address"
                value={props.rowdata?.address}
                onChange={ChangeHandeler}
                rules={[
                  { required: true, message: "Please input your Address !" },
                ]}
              />
            </Form.Item>
          </Col>

          <Col md={8} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item label="Mechanics Name :">
              <Input
                id="Zone"
                name="name"
                style={{ width: "100%" }}
                placeholder="Mechanics Name"
                value={props.rowdata?.name}
                onChange={ChangeHandeler}
                rules={[
                  {
                    required: true,
                    message: "Please input your Mechanics Name!",
                  },
                ]}
              />
            </Form.Item>
          </Col>
        </Row>
      </Form>
    </Modal>
  );
};

function mapStateToProps({ mechanics, mini }) {
  return {
    rowdata: mechanics.rowdata,
    model: mechanics.model,
    miniBanksList: mini.miniBanksList,
    // miniFirmList: mini.miniFirmList,
  };
}

export default connect(mapStateToProps, {
  mechanicAdd,
  isModelVisible,
  InputChangeValue,
  getMiniBanks,
})(AddMechanics);
